<template>
  <div>
        <div class="col-md-12 col-sm-12 col-xs-12">
          <v-form v-model="valid" ref="form">
          <div class="mb-5 mt-10">
            <div class="title">[필수] 개인정보 수집 및 이용 동의</div>
            <v-divider></v-divider>
          </div>
          <div>
            <v-expansion-panels accordion hover>
              <v-expansion-panel
              >
                <v-expansion-panel-header @click.native="expansionPanel()">
                  <div>
                    <v-checkbox v-model="checkbox1" label="개인정보 보호 동의(필수)" @click.native="check($event)"
                    :rules="[(v) => !!v || '개인정보 보호 동의 내용은 필수 입니다.']"
                    />
                  </div>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                  '(주)YA global'(이하 '회사')은 고객님의 개인정보를 중요시하며, <br />
                  “개인정보보호법” 및 "정보통신망 이용촉진 및 정보보호 등에 관한 법률” 등 관련 법령을 준수하고 있습니다.<br />
                  ‘회사’는 개인정보취급방침을 통하여 고객님께서 제공하시는 개인정보가 어떠한 용도와 방식으로 이용되고 있으며 <br />
                  개인정보보호를 위해 어떠한 조치가 취해지고 있는지 알려드립니다. <br />
                  ‘회사’는 개인정보취급방침을 개정하는 경우 웹사이트 공지사항(또는 개별공지)을 통하여 공지할 것입니다.<br />
                  - 본 방침은 2017년 8월 24일 부터 시행됩니다.<br /><br />
                  1. 개인정보의 수집 및 이용목적<br />
                  회사는 원활한 업무를 위하여 아래와 같이 개인정보를 수집합니다.<br />고객이 제공한 모든 정보는 명시된 목적 외 용도로 사용되지 않습니다.
                </v-expansion-panel-content>
              </v-expansion-panel>
              <v-expansion-panel>
                <v-expansion-panel-header>
                  <div>
                    <v-checkbox v-model="checkbox2" label="개인정보 수집·이용 동의(필수)" @click.native="check($event)"
                    :rules="[(v) => !!v || '개인정보 수집·이용에 관한 내용은 필수 입니다.']"
                    />
                  </div>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                  수집된 개인정보는 상품, 분양안내 및 상담, 이벤트 및 광고성 정보제공, 이벤트 진행 등의 마케팅 활동에 이용됩니다.
                </v-expansion-panel-content>
              </v-expansion-panel>
            </v-expansion-panels>
          </div>
          <div>
            <!-- <v-flex xs12 sm8 offset-sm2> -->
            <v-flex xs12 sm12>
              <div class="mb-5 mt-10">
            <div class="title">고객 정보 입력</div>
            <v-divider></v-divider>
          </div>
          <v-layout column>
            <v-flex>
              <v-text-field
                id="name"
                name="name"
                type="text"
                label="고객이름"
                v-model="user.name"
                :rules="nameRules"
              ></v-text-field>
            </v-flex>
            <v-flex>
              <v-text-field
                id="email"
                name="email"
                type="email"
                label="e-mail"
                v-model="user.email"
                :rules="emailRules"
              ></v-text-field>
            </v-flex>
            <v-flex>
              <v-text-field
                id="tel"
                name="tel"
                type="tel"
                label="전화번호(숫자만 입력)"
                v-model="user.tel"
                :rules="telRules"
              ></v-text-field>
            </v-flex>
            <v-flex>
              <v-textarea
                id="etc"
                name="etc"
                type="etc"
                label="문의하고 싶은 내용이 있으면 입력해 주세요."
                v-model="user.etc"
                autocomplete="off"
              ></v-textarea>
            </v-flex>
            <v-flex text-xs-center mt-5>
              <v-btn color="secondary" @click="submit">고객 등록</v-btn>
            </v-flex>
          </v-layout>

      </v-flex>
          </div>
          </v-form>
        </div>

    <v-dialog
      v-model="dialog"
      max-width="290"
    >
      <v-card>
        <v-card-title class="headline">고객정보 등록</v-card-title>

        <v-card-text>
          {{message}}
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <!-- <v-btn
            color="green darken-1"
            text
            @click="dialog = false"
          >
            Disagree
          </v-btn> -->

          <v-btn
            color="green darken-1"
            text
            @click="dialog = false"
            :loading="loading"
          >
            확인
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from 'querystring';
export default {
  data() {
    return {
      checkbox1: false,
      checkbox2: false,
      user: {},
      valid: false,
      dialog: false,
      nameRules: [
        v => !!v || '이름은 필수 입니다.',
        v => v && v.length <= 10 || '이름은 10 글자 이하로 작성되어야 합니다.',
      ],
      emailRules: [
        v => !!v || 'E-mail 은 필수입니다.',
        v => /.+@.+/.test(v) || '정상적인 E-mail 이 입력되어야 합니다.',
      ],
      telRules: [
        v => !!v || '전화번호는 필수입니다.',
        v => /^\d+$/.test(v) || '숫자만 입력되어야 합니다.',
      ],
      loading: false,
      message: '',
      formUrl: 'https://docs.google.com/forms/u/0/d/e/1FAIpQLSf9BL3Jz48qeflSD1jscadlyuG7VXiNcxVyj85ZR8TFc_E2wQ/formResponse',
      formEntry: {
        name: 'entry.1571130719',
        email: 'entry.1032548777',
        tel: 'entry.323479861',
        etc: 'entry.37639584'
      }
    }
  },
  methods: {
    check: function(e) {
      e.cancelBubble = true;
    },
    expansionPanel: function() {
    },
    async submit() {
      if (this.$refs.form.validate()) {
        const url =          this.formUrl;
        const body = {
          [this.formEntry.name]: this.user.name,
          [this.formEntry.email]: this.user.email,
          [this.formEntry.tel]: this.user.tel,
          [this.formEntry.etc]: this.user.etc,
        };
        try {
          this.loading = true;
          await this.postData(url, body);
          this.reset();
          this.message = '정상적으로 등록되었습니다.';
        } catch (error) {
          this.message = error.message;
        } finally {
          this.loading = false;
          this.dialog = true;
        }
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    postData(url, data) {
      return fetch(url, {
        method: 'POST',
        mode: 'no-cors',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: qs.stringify(data),
      });
    },
  }
}
</script>



